<template>
  <div class="mainPage">
    <div class="ictCharts">
      <div id="ict">

      </div>
    </div>
    <div class="fctCharts">
      <div id="fct">

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mainPage',
  data () {
    return {  
        intervalId1:"",
        intervalId2:"",
        ICtcode:[],
        ICTsum:[],
        ICTngAmount:[],
        ICTrate:[],
        FCtcode:[],
        FCTsum:[],
        FCTngAmount:[],
        FCTrate:[],  
        throughRateICTList: [],       
        throughRateFCTList: [],
		url:""
    }
  },
  methods:{
    getICTNewData(){
      console.log("我是获取ICT数据的方法");
	  this.url ='http://' + window.location.host;
	  console.log(this.url);
      this.$axios.get(`${this.url}/dafeng_mes/plan3/selectThroughRateICT.do`)
      .then(res => {
          console.log(res); 
          console.log(res.data.throughRateICTList); 
          this.throughRateICTList = res.data.throughRateICTList;
          console.log(this.throughRateICTList); 
          //this.getIctEchatsData();
          console.log("我是处理ICT数据的方法");
          this.ICTcode = [];
          this.ICTsum = [];
          this.ICTngAmount = [];
          this.ICTrate = [];
          for(let i = 0;i<this.throughRateICTList.length;i++){
            this.ICTcode.push(this.throughRateICTList[i].PRODUCT_CODE); 
            this.ICTsum.push(this.throughRateICTList[i].sumNum);
            this.ICTngAmount.push(this.throughRateICTList[i].errorNum);
            this.ICTrate.push(parseInt(this.throughRateICTList[i].rate));
          } 
          this.getIctCharts();       
      })
      .catch(err =>{
        console.log(err);
      })
    },
    // getIctEchatsData(){
    //   // console.log("我是处理ICT数据的方法");
    //   // for(let i = 0;i<this.throughRateICTList.length;i++){
    //   //     this.ICTcode.push(this.throughRateICTList[i].PRODUCT_CODE); 
    //   //     this.ICTsum.push(this.throughRateICTList[i].sumNum);
    //   //     this.ICTngAmount.push(this.throughRateICTList[i].errorNum);
    //   //     this.ICTrate.push(parseInt(this.throughRateICTList[i].rate));
    //   // }
    //   //   console.log(this.ICTcode);
    // },
    getFCTNewData(){
      console.log("我是获取FCT数据的方法");
	  this.url ='http://' + window.location.host;
	  console.log(this.url);
      this.$axios.get(`${this.url}/dafeng_mes/plan3/selectThroughRateFCT.do`)
      .then(res => {
          console.log(res);
          this.throughRateFCTList = res.data.throughRateFCTList;
          console.log(this.throughRateFCTList);  
          //this.getFctEchatsData();
          console.log("我是处理FCT数据的方法");
          this.FCTcode = [];
          this.FCTsum = [];
          this.FCTngAmount = [];
          this.FCTrate = [];
          for(let j = 0;j<this.throughRateFCTList.length;j++){
            this.FCTcode.push(this.throughRateFCTList[j].PRODUCT_CODE); 
            this.FCTsum.push(this.throughRateFCTList[j].sumNum);
            this.FCTngAmount.push(this.throughRateFCTList[j].errorNum);
            this.FCTrate.push(parseInt(this.throughRateFCTList[j].rate));
          }
          this.getFctCharts();
      })
      .catch(err =>{
        console.log(err);
      })
    },
    // getFctEchatsData(){
    //   // console.log("我是处理FCT数据的方法");
    //   // for(let i = 0;i<this.throughRateFCTList.length;i++){
    //   //     this.ICTcode.push(this.throughRateFCTList[i].PRODUCT_CODE); 
    //   //     this.ICTsum.push(this.throughRateFCTList[i].sumNum);
    //   //     this.ICTngAmount.push(this.throughRateFCTList[i].errorNum);
    //   //     this.ICTrate.push(parseInt(this.throughRateFCTList[i].rate));
    //   // }
    //   // console.log(this.FCTcode);
    // },
    getFctCharts(){
        console.log("我是绘制FCT图表的方法");
        // 基于准备好的dom，初始化echarts实例
        var myFctChart = this.$echarts.init(document.getElementById('fct'));
          var fctoption = { 
            title : {
              text: 'FCT直通率',
              x:60,
              y:10,
              textStyle:{
                color:'#fff',
                fontSize:20
              }
		        },        
            legend: {
                data:['总数','NG数','直通率'],
                textStyle: {
                  color: '#fff'
                }	
            },
            xAxis : [
                {
                    type : 'category',
                    axisLine: {
    	                show: false
    	              },
    	              axisLabel: {
                      show: true,
                      interval:0,
						          rotate:40,
    	                textStyle: {
    	                   color: '#fff',  //更改坐标轴文字颜色	    	                   
    	                }
    	              },
                    axisTick:{
                      show: false		   //显示/隐藏刻度
                    },	   
                    data : this.FCTcode
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name : '',
                    axisLine: {
    	                show: false
    	              },
    	              axisLabel: {
    	                show: true,
    	                textStyle: {
    	                   color: '#fff',  //更改坐标轴文字颜色	    	                   
                      },
                      //formatter: '{value} %'
    	              },
                    axisTick:{
                      show: false		   //显示/隐藏刻度
                    }	   
                }
            ],
            series : [

                {
                    name:'总数',
                    type:'bar',
                    barWidth:20,
                    itemStyle: {
                        normal: {
                          color:'#fadb14',
                            label: {
                                show: true,
                                position: 'top',
                                textStyle: {
                                    color: '#fff'
                                }
                            }
                        }
                    },
                    data:this.FCTsum
                },
                {
                    name:'NG数',
                    type:'bar',
                    barWidth:20,
                    itemStyle: {
                      normal: {
                        color:'#cf1322',
                          label: {
                              show: true,
                              position: 'top',
                              textStyle: {
                                  color: '#fff'
                              }
                          }
                      }
                    },
                    data:this.FCTngAmount
                },
                {
                    name:'直通率',
                    type:'line',                  
                    itemStyle:{
                      normal:{
                        color:'#eb2f96',
                        label:{
                          show:true,
                          formatter: function(a) {
                            var result = [],
                            counter = 0,
                            num = a.data;
                            num = (num || 0).toString().split('');
                            for (var i = num.length - 1; i >= 0; i--) {
                                counter++;
                                result.unshift(num[i]);
                                if (!(counter % 3) && i != 0) {
                                    result.unshift(',');
                                }
                            }
                          result.push("%");
                          result = result.join('');
                          return result;
                        }
                        }
                      }
                    },
                    //yAxisIndex: 0,
                    data:this.FCTrate
                }
            ]
          };
          myFctChart.setOption(fctoption);
    },
    getIctCharts(){
      console.log("我是绘制ICT图表的方法");
        // 基于准备好的dom，初始化echarts实例
        var myIctChart = this.$echarts.init(document.getElementById('ict'));
          var ictoption = {
            title : {
              text: 'ICT直通率',
              x:60,
              y:10,
              textStyle:{
                color:'#fff',
                fontSize:20
              }
		        }, 
            legend: {
                data:['总数','NG数','直通率'],
                textStyle: {
                  color: '#fff'
                }	
            },
            xAxis : [
                {
                    type : 'category',
                    axisLine: {
    	                show: false
    	              },
    	              axisLabel: {
                      show: true,
                      interval:0,
						          rotate:40,
    	                textStyle: {
    	                   color: '#fff',  //更改坐标轴文字颜色	    	                   
    	                }
    	              },
                    axisTick:{
                      show: false		   //显示/隐藏刻度
                    },	   
                    data : this.ICTcode
                }
            ],
            yAxis : [
                {
                    type : 'value',
                    name : '',
                    axisLine: {
    	                show: false
    	              },
    	              axisLabel: {
    	                show: true,
    	                textStyle: {
    	                   color: '#fff',  //更改坐标轴文字颜色	    	                   
                      },
                      //formatter: '{value} %'
    	              },
                    axisTick:{
                      show: false		   //显示/隐藏刻度
                    }	   
                }
            ],
            series : [

                {
                    name:'总数',
                    type:'bar',
                    barWidth:20,
                    itemStyle: {
                        normal: {
                          color:'#fadb14',
                            label: {
                                show: true,
                                position: 'top',
                                textStyle: {
                                    color: '#fff'
                                }
                            }
                        }
                    },
                    data:this.ICTsum
                },
                {
                    name:'NG数',
                    type:'bar',
                    barWidth:20,
                    itemStyle: {
                      normal: {
                        color:'#cf1322',
                          label: {
                              show: true,
                              position: 'top',
                              textStyle: {
                                  color: '#fff'
                              }
                          }
                      }
                    },
                    data:this.ICTngAmount
                },
                {
                    name:'直通率',
                    type:'line',                  
                    itemStyle:{
                      normal:{
                        color:'#eb2f96',
                          label:{
                          show:true,
                          formatter: function(a) {
                            var result = [],
                            counter = 0,
                            num = a.data;
                            num = (num || 0).toString().split('');
                            for (var i = num.length - 1; i >= 0; i--) {
                                counter++;
                                result.unshift(num[i]);
                                if (!(counter % 3) && i != 0) {
                                    result.unshift(',');
                                }
                            }
                          result.push("%");
                          result = result.join('');
                          return result;
                        }
                        }
                      }
                    },
                    //yAxisIndex: 0,
                    data:this.ICTrate
                }
            ]
          };
          myIctChart.setOption(ictoption);
    }
  },
  created(){
      //console.log("我是created钩子方法");
      this.getICTNewData();    
      this.getFCTNewData();

      this.intervalId1 = setTimeout(function(){				//每隔十分钟重新请求数据一次
		    this.getICTNewData();
	    setTimeout(arguments.callee,3000);
      },3000);

       this.intervalId2 = setTimeout(function(){				//每隔十分钟重新请求数据一次
		    this.getFCTNewData();
	    setTimeout(arguments.callee,3000);
      },3000);
          
  },
  beforeDestroy () {
      clearInterval(this.intervalId1)
      clearInterval(this.intervalId2)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mainPage{
  width: 100%;
  height: 100%;
  padding: 10px;
}
.fctCharts{
  width: 100%;
  height: 250px;
  border: 1px rgba(238, 238, 238, 0.4)  solid;
  border-radius: 5px; 
  margin-top: 5px;
}
#fct{
  width: 100%;
  height: 250px;
}
.ictCharts{
  width: 100%;
  height: 250px;
  border: 1px rgba(238, 238, 238, 0.4)  solid;
  border-radius: 5px; 
}
#ict{
  width: 100%;
  height: 250px;
}
</style>



